body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  background-color: #212429;
  color: white;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  width: 100%;
}

.react-logo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
  height: calc(10px + 2vmin);
  pointer-events: none;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  -webkit-align-items: center;
          align-items: center;
  background-color: #212429;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  height: 10vh;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  box-sizing: border-box;
  -webkit-flex: 1 0 90%;
          flex: 1 0 90%;
  margin: 1rem;
  transition: 0.3s;
}

.card img {
  height: 15rem;
  width: 100%;
}

@media screen and (min-width: 576px) {
  .card {
    max-width: calc(50% -  2rem);
  }
}

@media screen and (min-width: 768px) {
  .card {
    max-width: calc(33% -  2rem);
  }
}

@media screen and (min-width: 992px) {
  .card {
    max-width: calc(25% - 2rem);
  }
}

@media screen and (min-width: 1200px) {
  .card {
    max-width: calc(20% - 2rem);
  }
}

.card:hover {
  box-shadow: 0 16px 32px 0 rgba(0,0,0,0.4);
}

.cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.collapsible {
  background-color: #25282F;
  border: 2px solid #282c34;
  color: white;
  height: 5vh;
  padding: 0;
  width: 100%;
}

.opened:after {
  content: '\2795'; /* Unicode character for "plus" sign (+) */
  color: white;
  float: right;
  margin-right: 5px;
}

.closed:after {
  content: "\2796"; /* Unicode character for "minus" sign (-) */
  color: white;
  float: right;
  margin-right: 5px;
}

.collapsible:hover, .collapsible:focus {
  outline: none;
}

.collapsible:hover {
  cursor: pointer;
}

.row {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.hidden {
  display: none;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  width: 100%;
}

.loader {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  border: 1vmin solid #f3f3f3; /* Light grey */
  border-top: 1vmin solid #555; /* Blue */
  border-radius: 50%;
  height: calc(10px + 2vmin);
  margin: auto;
  width: calc(10px + 2vmin);
}
  
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
  
@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.app {
  background-color: #282c34;
  min-height: 100vh;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

